$bodycolor: #E5E5E5;
$sectioncolor: #ffffff;

$textcolorwhite: #ffffff;
$textcolorgrey: #8999A5;
$textcolorgreen: #2F9626;
$tablecolorgrey: #606483;
$textcolordark: #232056;
$textcolordarkgrey: #767D86;
$textcolorblack: #1b1c1e;
$textcolorpopupdark: #1E1D2E;
$textcolorsubmenudark: #334866;
$textcolorblue: #5FB6E7;



$backcolororange: #FFA438;
$backcolororangehover: #FF9316;
$backcolorcyan: #5FB6E7;
$backcolorlime: #AFDDAB;
$backbuttongray: #EEF2F4;
