/* ------ mobile styles -------*/

@media screen and (max-width: 992px)  {
    body {
        width: 100%;
        height: auto;
        background-color: $bodycolor;
    
        font-family: 'Inter', sans-serif;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
    
        overflow-x: hidden;
    }

    .wrapper {
        width: 375px;
        height: auto;    
        margin: 0 auto;
        background-color: $sectioncolor;
    }
    
    .main {
        width: 375px;
        height: auto;
        background-color: $sectioncolor;
    }
    
    .aside {
        width: 375px;
        height: auto;
        
        background-color: $sectioncolor;
    }
    
    .container {
        width: 345px;
        height: auto;
        margin: 0 auto;
    }
    
    // header
    
    .header_info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    
    .person_photo {
        margin-right: 19px;
        order: 2;
    }
    
    .indicator {
        position: absolute;
        top: 180px;
        left: 190px;
        z-index: 1;
    }
    
    .personal_info {
       width: 375px;    
       display: flex;
       flex-direction: column;
       justify-content: center;
       align-items: center;
       order: 3;
    }
    
    .personal_info_name {
        font-weight: 500;
        font-size: 22px;
        line-height: 24px;
    }
    
    .personal_info_sex,
    .personal_info_time {
        transform: translateY(3px);
    }
    
    .personal_info_location {
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;    
    }
    
    .personal_info_location {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;    
    }
    
    .grey {
        color: $textcolorgrey;
    }
    
    .green {
        color: $textcolorgreen;
    }
    
    .cvmenu {
        margin-top: 15px;
        justify-self: flex-end;
        align-self: stretch;
        position: relative;
    }
    
    .cvmenu_file,
    .cvmenu_download {
        margin-right: 16px;
    }
    
    .cvmenu_file {
        margin-left: 216px;
        cursor: pointer;
    }

    .cvmenutop {
        width: 375px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        background-color: $sectioncolor;
        z-index: 1;
    }

    .cvmenutop_name {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: #232056;

    }

    .cvmenutop_trade {
        font-weight: 400;
        font-size: 13px;
        line-height: 18px;
        color: #232056;

    }
    
    // jobdescription
    
    .jobdescription_section {
        background-color: $sectioncolor;
    }
    
    .jobdescription {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
    .jobdescription_title {
        font-weight: 500;
        font-size: 22px;
        line-height: 24px;
    }
    
    .jobdescription_text {
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
    }
    .jobdescription_list {
        margin-left: -20px;
    }
    
    .jobdescription_row1 td,
    .jobdescription_row3 td,
    .jobdescription_row5 td,
    .jobdescription_row7 td {
        width: 300px;
        margin-top: 16px;
        color: $tablecolorgrey;
    }
    
    // job
    
    .job_section {
        margin-top: 32px;
        background-color: $sectioncolor;
    }
    
    .job,
    .job_block {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }
    
    .job_title {
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        color: #232056;
        
    }
    
    .job_title span {
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
    }
    
    .job_block_title1 {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: $textcolordark;
    }

    
    .job_block_title2 {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: $tablecolorgrey;
        position: absolute;
        right: 0;
    }
    
    .job_block_firmtext,
    .job_block_jobtext {
        margin-left: 9px;
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;

    }
    
    .job_block_joblink {
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: $textcolorgrey;
    }

    .job_block_hidelink,
    .job_block_showlink {
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: $textcolorblue;
    }

    .job_block_dutiestext {
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
    }

    .job_block_achievetext,
    .job_block_leavetext {
        font-size: 16px;
        line-height: 22px;
        font-weight: 500;
    }
    
    .job_block_dutieslist {
        color: $tablecolorgrey;
        margin-left: -12px;
    }

    .job_block_achievelist,
    .job_block_leavelist {
        margin-left: -12px;
    }
    
    .job_block_portfolio {
        margin-top: 30px;
    }
    
    .job_block_portfolio_title {
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
    }
    
    .job_block_portfolio_gallery {
        width: 345px;
        height: 205px;
    }
    
    // popupmobile portfolio
    
    .popupmobile_portfolio {
        opacity: 0;
    
        height: auto;
        width: 375px;
        position: absolute;
        top: 1700px;
        left: 28%;
        z-index: -1;
    
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    
        background-color: #1B1C1EBF;
    }
    
    .popup_portfolio_toptext {
    
        font-weight: 600;
        font-size: 18px;
        line-height: 46px;
        color: $textcolorwhite;
        display: block;
        width: 375px;
        height: 46px;
        text-align: center;
    }


    .popupmobile_portfolio_pictblock {
        width: 375px;
        height: 500px;
        overflow: hidden;
    }
    
    .popup_portfolio_title {    
        margin-bottom: 20px;
        margin-left: 20px;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: $textcolorwhite;
    }    
    
    .popup_portfolio_price {
        margin-top: 20px;
        margin-left: 20px;
    
        font-weight: 500;
        font-size: 24px;
        line-height: 32px;
        color: $textcolorwhite;
    }
    
    .popupmobile_portfolio_close {
        position: absolute;
        top: 10px;
        left: 10px;
    }
    
    .salary_sum {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
    }
        
    .vacancy {
        width: 345px;
        height: auto;
        padding: 15px;
        position: relative;
        
        order: 4;

        margin-top: 25px;
    }

    // .vacancybuttons {
    //     position: sticky;
    //     bottom: 0px;
    // }
    
    .vacancy_button {
        display: block;
        width: 287px;
        height: 42px;
        border-radius: 8px;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        color: $textcolorwhite;
        cursor: pointer;
    }
    
    .vacancy .vacancy_button:hover .vacancy .vacancysubmenu{
        display: block;
    }
    
    .orange {
        background-color: $backcolororange;
        color: $textcolorwhite;
    }
    
    .orange:hover {
        background-color: $backcolororangehover;
    }
    
    .vacancy_favourite {
        display: block;
        width: 40px;
        height: 40px;
        background: url(./image/logo/favourite.png) center no-repeat;
        position: absolute;
        top: 15px;
        right: 15px;
        cursor: pointer;
    }
    
    // .vacancy_favourite:hover {
    //     background: url(./image/logo/favourite_hover.png) center no-repeat;
    
    // }
    
    .vacancysubmenu {
        width: 234px;
        height: auto;
        background-color: $sectioncolor;
        position: relative;
        display: none;
    }
    
    .vacancysubmenu_list {
        list-style: none;
        margin-left: -32px;
    }
    
    .vacansysubmenu_point {
        left: 0;
        display: block;
        margin: 2px 8px;
        padding: 8px;
        cursor: pointer;
    }

    .vacansysubmenu_point:last-child {
        text-align: center;
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: $textcolorsubmenudark; 
    }
    
    .vacancysubmenu_image {
        margin-right: 8px;
        transform: translateY(3px);
        font-weight: 400;
        font-size: 13px;
        line-height: 18px;
    }
    
    .favouritesave {
        background: url(./image/logo/favourite_save.png) center no-repeat;
    }
    
    .videointerview {
        width: 345px;
        height: 74px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        margin-top: 16px;
        border-bottom: 1px solid #E7EEF6;
        box-sizing: border-box;
        border-radius: 8px; 
    }
    
    .videointerview_text {
        margin-left: 12px;
    }
    
    .videointerview_maintext {
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
        color: $textcolordark;
    
    }
    
    .videointerview_secondtext {
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: $textcolorgrey;
    
    }

    .videointerview_arrow {
        width: 40px;
        height: 40px;
        background: url(./image/logo/arrowdown.png) center no-repeat;
    }
    
    .feedback {
        width: 345px;
        height: auto;
    
        margin-top: 32px;
    
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    
        border: 1px solid #E7EEF6;
        box-sizing: border-box;
        border-radius: 10px;
    }
    
    .feedback_title {
        font-family: Source Sans Pro, sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 50px;
        text-transform: uppercase;
        margin-left: 20px;
        color: $textcolordarkgrey;
        cursor: pointer;
    }
    
    .feedback_logo {
        margin-left: 80px;
        transform: translateY(5px) rotate(90deg) ;
    }
    
    .feedback_card {
        display: block;
        width: 345px;
        height: 47px;
        padding: 10px 20px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
    }
    
    .line {
        width: 46.49px;
        height: 0px;
        left: 77px;
        top: 56.49px;
        transform: rotate(-90deg);
    }
    
    .cyancolor {
        background-color: $backcolorcyan;
        border: 3px solid $backcolorcyan;
    
    }
    
    .orangecolor {
        background-color: $backcolororange;
        border: 3px solid $backcolororange;
    }
    
    .limecolor {
        background-color: $backcolorlime;
        border: 3px solid $backcolorlime;
    }
    
    .card_date {
        width: 47px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    
    .card_datetext {
        font-weight: 500;
        font-size: 12px;
        line-height: 20px;
        color: $textcolorblack;
    }
    
    .card_descriptionsecondtext {
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: $textcolorgrey;
    
    }
    
    .card_descriptionfirsttext {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
        color: $textcolorblack;
    }
    
    .popup {
        width: 327px;
        height: 274px;
        border-radius: 16px;
        padding: 24px;
        border: 1px solid $textcolordark;
    
        background-color: $sectioncolor;
    
        position: absolute;
        top: 30px;
        left: -15px;
        z-index: 10;
    
        display: none;
    }
    
    .popup_title {
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
        text-align: left;
        color: $textcolordark;
    }
    
    .popup_textarea {
        width: 327px;
        height: 138px;
        border: 1px solid $backbuttongray;
    }
    
    .button_popup {
        height: 42px;
        width: 149px;
        border-radius: 6px;
        padding: 0px, 20px, 0px, 20px;
        margin: 0 4px;
        border: 0;
    }
    
    .gray {
        background-color: $backbuttongray;
        color: $tablecolorgrey
    }
    
    .gray:hover {
        background-color: $backbuttongrayhover;
     
    }

    .desktop {
        display: none;
    }

    .scrollblock {
        position: sticky;
        top: 0;
    }

}


