.feedback {
    width: 290px;
    height: auto;

    margin-top: 32px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    border: 1px solid #E7EEF6;
    box-sizing: border-box;
    border-radius: 10px;
}

.feedback_title {
    font-family: Source Sans Pro, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 50px;
    text-transform: uppercase;
    margin-left: 20px;
    color: $textcolordarkgrey;
    cursor: pointer;
}

.feedback_logo {
    margin-left: 80px;
    transform: translateY(5px) rotate(90deg) ;
}

.feedback_card {
    display: block;
    width: 250px;
    height: 47px;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.line {
    width: 46.49px;
    height: 0px;
    left: 77px;
    top: 56.49px;
    transform: rotate(-90deg);
}

.cyancolor {
    background-color: $backcolorcyan;
    border: 3px solid $backcolorcyan;

}

.orangecolor {
    background-color: $backcolororange;
    border: 3px solid $backcolororange;
}

.limecolor {
    background-color: $backcolorlime;
    border: 3px solid $backcolorlime;
}

.card_date {
    width: 47px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.card_datetext {
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    color: $textcolorblack;
}

.card_descriptionsecondtext {
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: $textcolorgrey;

}

.card_descriptionfirsttext {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: $textcolorblack;
}



.social {
    width: 290px;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 32px;
}

.popup {
    width: 394px;
    height: 288px;
    border-radius: 16px;
    padding: 24px;
    border: 1px solid $textcolordark;

    background-color: $sectioncolor;

    position: absolute;
    top: 30%;
    left: 5%;
    z-index: 1;

    display: none;
}

.popup_title {
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    text-align: left;
    color: $textcolordark;
}

.popup_textarea {
    width: 385px;
    height: 178px;
    border: 1px solid $backbuttongray;
}

.button_popup {
    height: 42px;
    width: 189px;
    border-radius: 6px;
    padding: 0px, 20px, 0px, 20px;
    margin: 0 4px;
    border: 0;
}

.gray {
    background-color: $backbuttongray;
    color: $tablecolorgrey
}

.gray:hover {
    background-color: $backbuttongrayhover;
 
}

.mobile {
    display: none;
}

